@media screen and (max-width: 740px), screen and (max-device-width: 740px){
  body{
    .header{
      .container{
        .header__nav-panel{
          &__goods{
            .dropMenu {
              width: 60%;
              top: 0;
              left: 300px;
              li{
                position: relative;
                margin: 5px 0 0 25px;
                width: 100%;
                ul{
                  display: none;
                }
                &:hover{
                  ul{
                    li{
                      display: none;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}